<template>
    <section id="contact" class="contact-wrapper">
        <pageTitle title="Contact" />
        <div class="box-wrapper">
            <div class="table">
            <div class="tbody">
            <form>
            <div class="tr">
            <div class="th">Name</div>
            <div class="td"><input class="form-field" v-model="onesDatas.name" placeholder="Required"></div></div>
            <div class="tr">
            <div class="th">Address</div>
            <div class="td"><input class="form-field" v-model="onesDatas.mail" placeholder="Required"></div></div>
            <div class="tr">
            <div class="th">Subject</div>
            <div class="td"><input class="form-field" v-model="onesDatas.subject"></div></div>
            <div class="tr">
            <div class="th">Message</div>
            <div class="td"><textarea class="form-field" v-model="onesDatas.message" placeholder="Required"></textarea></div></div>
            <div class="tr">
            <div class="th"></div>
            <div class="td">
              <v-btn 
              class="form-field"
              target="_blank"
              @click="opneDialog"
              >Preview</v-btn>
              <v-btn 
              class="form-field"
              target="_blank"
              @click="submit"
              >Submit</v-btn></div>
            </div>
            </form>
            </div>
            </div>
        </div>
        <Preview
        :prev-dialog="preview"
        ;ones-datas="onesDatas"
        @openDialog="toggleCheck(check)"
        />
    </section>
</template>

<script>
import pageTitle from '@/components/parts/pageTitle'
import Preview from '../parts/preview'

export default {
    name: 'Contact',
    components: {
        pageTitle,
        Preview,
    },
    data:() =>({
        snackbar: true,
        preview: false,
        onesDatas: {},
    }),
    methods: {
        openDialog: function(){
            this.snackbar = !this.snackbar
            this.preview = !this.preview
            scrollBy(0,0)
        },
        submit: async function(){
            var params = new URLSearchParams()
            params.append('name', this.name)
            params.append('mail', this.mail)
            params.append('subject', this.subject)
            params.append('message', this.message)
            await this.axios
            .post('/api/contact', params)
            location.href = '/contact/accepted'
        },
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/_color.scss';
@import '@/assets/styles/_mixin.scss';

.contact-wrapper {
    @include section;
}

.box-wrapper {
  @include box-wrapper;
  color: $text-color;

  .table {
    text-align: left;
  }

  .th {
    padding-right: 20px;
    font-weight: normal;
    color: $sub-text-color;
  }

  .td {
    font-size: 20px;
    padding: 5px 8px 5px 0;
    display: flex;
  }
}

.form-field {
  background-color: darken(white,10%);
  display: flex;
}

/*スマートフォン*/
@media (max-width: 670px) {
  .box-wrapper {
    .td {
      display: block;
      font-size: 16px;
      width: 100%;
      padding: 5px 0;
      border-bottom: none;
    }
  }
}
</style>
